.miofa-with-message {
    width: 120px;
    position: relative;
    overflow: visible;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.message-container {
    width: 180px;
    height: 50px;
    z-index: 10;
    left: -40px;
    position: absolute;
    top: -30px;
    border: 1px solid rgb(230, 37, 37);
    background-color: white;
    color: black;
    padding: 10px;
    font-size: 14px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
